<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div class="alert alert-success alert-dismissible fade show" role="alert">
      (Juste, partager quelques expériences de «techos»(*))
    </div>
    <h2>Note d'intention du projet 13</h2>
    Avec Digdiscord, je teste la mise en place d’un service web selon un principe architectural très simple :<br>
    la séparation de la charge  entre le «front», le «back» et le «reste».<br>
    A l’instar de l’inaltérable motif architectural MVC qui distingue les 3 responsabilités d’un logiciel (Modèle, Vue et Contrôleur), la conception de ce service web peu simplement se définir ainsi (**):
    <ul>
      <li>
        Récupérer de l’information.
      </li>
      <li>
        Délivrer des services.
      </li>
      <li>
        Etre accessible.
      </li>
    </ul>
Ces objectifs se traduisent respectivement par les actions suivantes :<br>
    <ul>
      <li>
        Collecte des commentaires, URL, "snipets", utilisateurs, dates, etc. dans un forum Discord grâce à son ’API ouverte.
      </li>
      <li>
        Génération des agrégats analytiques ou ludiques.
      </li>
      <li>
        Présentation l’information sous forme dynamique via des listes ou de graphiques.
      </li>
    </ul>
Et chacune de ces responsabilités correspond à une solution technique bien déterminée :<br>
    <ul>
      <li>
La collecte est effectuée par une <a href='https://docs.djangoproject.com/fr/3.1/howto/custom-management-commands/'>«commande managée»</a> de Django<br>
Cette commande peut être automatisée par un mécanisme tiers (ex : par cron)
      </li>
      <li>
Les agrégats sont générés naturellement via l’ORM de Django (***) et se transmettent à la couche présentation par les sérialiseurs DRF<br>
      </li>
      <li>
L’information est restituée sous forme de graphiques Vue-JS ou de «cards bootstrap»<br>
      </li>
    </ul>
Le projet est donc basé sur ce triptyque : Django, DRF, Vue-JS<br>
Plus précisément la pile technique comprend :<br>
    <ul>
      <li>
Django / Django Rest Framework
      </li>
      <li>
Vue-JS
      </li>
      <li>
Bootstrap-vue
      </li>
      <li>
VueChart-Js
      </li>
      <li>
Axios
      </li>
      <li>
Swagger-ui
      </li>
      <li>
    </ul>
+ accessoirement pour l’aspect bonne pratique :
pre-commit
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Bienvenue dans cabane du vieux developpeur'
    }
  }
}
</script>

